.hovered-lines-holder {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.hovered-lines {
  position: absolute;
  right: 0;
  width: 300px;
  min-height: 100px;
  -ms-flex-item-align: flex-center;
      align-self: flex-center;
  -webkit-transform: translate(calc(300px + 2em), 0);
      -ms-transform: translate(calc(300px + 2em), 0);
          transform: translate(calc(300px + 2em), 0);
  background-color: #FFF;
  text-align: left;
  padding-left: 1em;
  padding-right: 1em;
  -webkit-transition: -webkit-transform .33s ease;
  transition: -webkit-transform .33s ease;
  -o-transition: transform .33s ease;
  transition: transform .33s ease;
  transition: transform .33s ease, -webkit-transform .33s ease;
}
.hovered-lines .speed {
  font-style: italic;
  font-weight: bold;
}
.hovered-lines--opened {
  -webkit-transform: translate(0,0);
      -ms-transform: translate(0,0);
          transform: translate(0,0);
}
.hovered-lines p {
  margin-top: 0.5em;
}
.hovered-lines h4 {
  margin-bottom: 0;
}



